 width: 100%;\n  }\n\n  &.transparent {\n    background: transparent;\n    border: 0;\n    height: auto;\n  }\n\n  &.compact {\n    padding: 0;\n  }\n}\n","// Font sizes\n$font-size-micro: 0.5rem;\n$font-size-mini: 0.625rem;\n$font-size-small: 0.75rem;\n$font-size-default: 0.875rem;\n$font-size-medium: 1rem;\n$font-size-large: 1.25rem;\n$font-size-big: 1.5rem;\n$font-size-bigger: 2rem;\n$font-size-mega: 2.5rem;\n$font-size-giga: 3.5rem;\n\n// spaces\n$zero: 0;\n$space-micro: 0.125rem;\n$space-smaller: 0.25rem;\n$space-small: 0.5rem;\n$space-one: 0.625rem;\n$space-slab: 0.75rem;\n$space-normal: 1rem;\n$space-two: 1.25rem;\n$space-medium: 1.5rem;\n$space-large: 2rem;\n$space-larger: 3rem;\n$space-big: 4rem;\n$space-jumbo: 5rem;\n$space-mega: 6.25rem;\n\n// font-weight\n$font-weight-feather: 100;\n$font-weight-light: 300;\n$font-weight-normal: 400;\n$font-weight-medium: 500;\n$font-weight-bold: 600;\n$font-weight-black: 700;\n\n// Colors\n$color-woot: #fc7658;\n$color-primary: $color-woot;\n$color-gray: #6e6f73;\n$color-light-gray: #999a9b;\n$color-border: #e0e6ed;\n$color-border-transparent: rgba(224, 230, 237, 0.5);\n$color-border-light: #f0f4f5;\n$color-border-dark: #cad0d4;\n$color-background: #f4f6fb;\n$color-background-light: #fafafa;\n$color-white: #fff;\n$color-body: #3c4858;\n$color-heading: #1f2d3d;\n$color-error: #ff382d;\n$color-success: #44ce4b;\n\n// Color-palettes\n\n$color-primary-light: #c7e3ff;\n$color-primary-dark: darken($color-woot, 20%);\n\n// Spinner\n$spinkit-spinner-color: $color-white !default;\n$spinkit-spinner-margin: 0 0 0 1.6rem !default;\n$spinkit-size: 1.6rem !default;\n\n// Snackbar default\n$woot-snackbar-bg: #323232;\n$woot-snackbar-button: #ffeb3b;\n\n$swift-ease-out-duration: 0.4s !default;\n$swift-ease-out-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1) !default;\n$swift-ease-out: all $swift-ease-out-duration $swift-ease-out-timing-function !default;\n\n$border-radius: 0.1875px;\n$line-height: 1;\n$footer-height: 11.2rem;\n$header-expanded-height: $space-medium * 10;\n\n$font-family: 'Inter', -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI',\n  Roboto, 'Helvetica Neue', Tahoma, Arial, sans-serif;\n\n// Break points\n$break-point-medium: 667px;\n","// scss-lint:disable PropertySortOrder DeclarationOrder QualifyingElement\n$form-border-width: 1px;\n$input-height: $space-two * 2;\n\n.form-input {\n  @include placeholder {\n    color: $color-gray;\n  }\n\n  appearance: none;\n  background: $color-white;\n  border: 1px solid $color-border;\n  border-radius: $border-radius;\n  box-sizing: border-box;\n  color: $color-body;\n  display: block;\n  font-family: $font-family;\n  font-size: $font-size-medium;\n  height: $input-height;\n  line-height: 1.5;\n  max-width: 100%;\n  outline: none;\n  padding: $space-smaller;\n  position: relative;\n  transition: background .2s,\n  border .2s,\n  box-shadow .2s,\n  color .2s;\n  width: 100%;\n\n  &:focus {\n    border-color: $color-primary;\n  }\n\n  &::placeholder {\n    color: $color-gray;\n  }\n\n  // Input sizes\n  &.small {\n    font-size: $font-size-small;\n    height: $space-large;\n    padding: $space-small $space-one;\n  }\n\n  &.default {\n    font-size: $font-size-default;\n    height: $space-medium;\n    padding: $space-smaller $space-slab;\n  }\n\n  &.large {\n    font-size: $font-size-medium;\n    height: $space-larger;\n    padding: $space-slab $space-two;\n  }\n\n  &.input-inline {\n    display: inline-block;\n    vertical-align: middle;\n    width: auto;\n  }\n\n  // Input types\n  &[type=\"file\"] {\n    height: auto;\n  }\n}\n\n// Form element: Textarea\ntextarea.form-input {\n  font-family: $font-family;\n\n  @include placeholder {\n    color: $color-light-gray;\n  }\n\n  &,\n  &.large,\n  &.small {\n    height: auto;\n  }\n}\n","// scss-lint:disable PseudoElement SpaceBeforeBrace VendorPrefix\n$shadow-color-1: rgba(50, 50, 93, 0.08);\n$shadow-color-2: rgba(0, 0, 0, 0.07);\n$shadow-color-3: rgba(50, 50, 93, 0.08);\n$shadow-color-4